import { Component, OnInit } from '@angular/core';
import { _HttpClient } from '@delon/theme';
import { NzModalService, NzNotificationService } from 'ng-zorro-antd';
import { MessageHub } from '../../../service/MessageHub';
import { ConnectPipe } from './connect.pipe'
import { ReactiveFormsModule, FormBuilder,
    FormGroup,
    Validators,
    FormControl } from '@angular/forms';
@Component({
  selector: 'app-messagehub',
  templateUrl: './messagehub.component.html',
  styleUrls: ['./messagehub.component.less'],
  providers: [MessageHub, ReactiveFormsModule, ConnectPipe]
})
export class MessagehubComponent implements OnInit {
    poolLists: any = []; // 
    queueLists: any = []; // 
    connectionLists: any = []; // 
    poolFormData: any = {}; // 
    clearPoolToggle = false; // 
    clearPoolQueueToggle = false; // 
    PoolIng: any = {}; // 
    PoolIngConnection: any = []; // 
    tabIng = 1; //tab
    // 
    poolQueueFormData: any = {
        isCreate: false,
        key: '',
        value: ''
    };
    // ()
    toggleDetails(expand) {
        for(let i = 0; i < this.queueLists.length; i++) {
            this.queueLists[i].expand = expand
        }
    }
    // 
    openCreatePoolModal() {
        this.clearPoolToggle = true;
        this.poolFormData.reset();
    }
    // 
    createPool(data: any) {
        const obj = {
            poolName: data.poolname,
            poolMode: data.pooltype,
            messagesSortColumn: data.poolsort,
            forceOverWrite  : data.forceOverWrite,
            description: data.pooldescription
        }
        console.log(obj);
        this.MessageHub.createPool(
            obj,
            (res: any) => {
        });
        this.clearPoolToggle = false;
    }
    // 
    delectPool(poolName) {
        this.modal.confirm({
            title: '',
            content: '',
            okText: '',
            cancelText: '',
            onOk: () => {
                this.MessageHub.destroyPool(poolName, (res: any) => {

                });
            },
            onCancel: () => {
            }
        });
    }
    // 
    flushPool(poolName) {
        this.modal.confirm({
            title: '',
            content: '',
            okText: '',
            cancelText: '',
            onOk: () => {
                this.MessageHub.flushPool(poolName, (res: any) => {
                })
            },
            onCancel: () => {
            }
        });
    }
    // 
    monitorPoolQueue(pool) {
        // 
        if(this.PoolIng.poolName) {
            this.MessageHub.unlistenPool(this.PoolIng.poolName, (res) => {

            });
        }
        this.PoolIng = pool;
        this.MessageHub.listenPool(this.PoolIng.poolName, (res) => {

        });
    }
    // 
    openCreatePoolQueueModal(data?: any) {
        // 
        if(data) {
            this.clearPoolQueueToggle = true;
            this.poolQueueFormData.key = data.key;
            this.poolQueueFormData.value = data.value
            this.poolQueueFormData.isCreate = false
        } else {
            this.clearPoolQueueToggle = true;
            this.poolQueueFormData.key = this.MessageHub.createGuid();
            this.poolQueueFormData.value = '';
            this.poolQueueFormData.isCreate = true;
            this.poolQueueFormData.errTxt = '';
        }
    }
    // 
    createPoolQueue() {
        if(this.poolQueueFormData.isCreate) {
            // 
            const obj = {
                poolName: this.PoolIng.poolName,
                message: {
                    key: this.poolQueueFormData.key,
                    // value: this.poolQueueFormData.value
                    value: this.poolQueueFormData.value
                }
            }
            this.MessageHub.createMessage(obj, (res: any) => {
            });
        }else {
            // 
            const obj = {
                poolName: this.PoolIng.poolName,
                message: {
                    key: this.poolQueueFormData.key,
                    value: this.poolQueueFormData.value
                }
            }
            this.MessageHub.updateMessage(obj, (res: any) => {
            });
        }
        this.clearPoolQueueToggle = false;
    }
    // 
    deleteMessage(key) {
        const obj = {
            poolName: this.PoolIng.poolName,
            messageKey: key
        }
        this.MessageHub.deleteMessage(obj,(res: any) => {
            // this.notice.create(res.code===200?'success':'error','',res.message)
        })
    }
    selectTab(i) {
        this.tabIng = i
    }
    constructor(
        private http: _HttpClient,
        private fb: FormBuilder,
        private MessageHub: MessageHub,
        private notice: NzNotificationService,
        private modal: NzModalService
    ) { }

    ngOnInit() {
        this.MessageHub.user.clientName = 'yyyyy'
        console.log(this.MessageHub.user.clientName);
        // 
        this.MessageHub.listenPoolList((data: any) => {
            console.log(data);
            this.poolLists = data;
            // 
            for (let i = 0; i < this.poolLists.length; i++) {
                if(this.poolLists[i].poolName === this.PoolIng.poolName) {
                    this.PoolIng = this.poolLists[i];
                    break;
                }
            }
        });
        // 
        this.MessageHub.receivePoolInfo((data: any) => {
            this.queueLists = data.messages;
        });
        // 
        this.MessageHub.listenConnectionList((data: any) => {
            this.connectionLists = data;
        });
        // 
        this.poolFormData = this.fb.group({
            poolname            : [ null, [ Validators.required ] ],
            pooldescription         : [ null, [ Validators.required ] ],
            pooltype            : [ 'publicMode' ],
            poolsort            : [ 'createTime' ],
            forceOverWrite      : [true]
        });
    }

}



